        <div class="text-secondary mb-3">
            Плавание в широком смысле слова – это и олимпийский водный вид спорта, и отличный вариант укрепления здоровья, и один из типов базового навыка и физической активности. Уникальность плавания заключается в слаженной работе всех групп мышц независимо от выбранного стиля. Поэтому оно и завоевало особую популярность в качестве динамического спортивного вида и любимого занятия для поддержания здорового образа жизни.
        </div>

        <div v-if="currentMenuItem === 'tech'" class="text-primary fw-bold">
            Техника безопасности 
            <div class="text-secondary fw-normal p-0 mt-3 mb-4">
                На уроке физической культуры в бассейне для студентов колледжа и университета безопасность играет ключевую роль. Ведь вода может быть опасной средой, особенно для тех, кто не умеет плавать или имеет ограничения по здоровью. Поэтому необходимо соблюдать определенные меры предосторожности, чтобы каждый участник занятия мог наслаждаться тренировкой и не подвергать себя риску. <br><br>

                1.	Перед началом занятия важно проверить состояние бассейна. Убедитесь, что вода чистая и прозрачная, а система фильтрации работает должным образом. Если вы замечаете какие-либо проблемы, немедленно сообщите об этом инструктору или администрации бассейна. <br><br>

                2.	Перед входом в бассейн каждый студент должен быть ознакомлен с правилами безопасности. Обязательно убедитесь, что каждый понимает их и готов соблюдать. Правила могут включать запрет на бег по краю бассейна, прыжки с вышки без разрешения инструктора, использование спасательного оборудования только по назначению и т.д. <br><br>

                3.	Перед началом урока каждый студент должен пройти проверку наличия навыков плавания. Если кто-то не умеет плавать или имеет ограничения по здоровью, ему следует немедленно сообщить об этом инструктору. В таких случаях инструктор может предложить альтернативные упражнения или назначить дополнительного спасателя для обеспечения безопасности. <br><br>

                4.	Важно носить правильную одежду и обувь для занятий в бассейне. Избегайте слишком свободных или тяжелых нарядов, которые могут затруднять движение в воде. Обувь должна быть удобной и обеспечивать хорошую фиксацию стопы, чтобы предотвратить возможные травмы. <br><br>

                5.	Всегда следуйте инструкциям инструктора и не отклоняйтесь от предписанных тренировок. Они разработаны с учетом безопасности и максимальной эффективности тренировки. Не пытайтесь выполнять сложные упражнения без необходимых навыков или под риском получения травмы. <br><br>

                6.	Не забывайте о правилах личной гигиены. Перед входом в бассейн принимайте душ, чтобы удалить с кожи грязь и масла, которые могут загрязнить воду. Также регулярно мойте руки с мылом и водой, чтобы предотвратить распространение бактерий. <br><br>

                7.	При возникновении любых проблем или чувстве дискомфорта обязательно сообщайте об этом инструктору. Не стесняйтесь задавать вопросы или просить помощи, если что-то непонятно или вызывает беспокойство. <br><br>

                8.	Не забывайте о правильном питании и гидратации перед и после занятий в бассейне. Вода и питательные вещества играют важную роль в поддержании энергии и общего благополучия организма. Поэтому убедитесь, что вы употребляете достаточное количество воды и сбалансированной пищи, чтобы поддерживать свое физическое состояние. <br><br>

                9.	Не забывайте об общей безопасности вокруг бассейна. Убедитесь, что пол на краю бассейна не скользкий, чтобы избежать падений и травм. Также следите за другими участниками занятий, особенно если они находятся в воде, чтобы предотвратить возможные столкновения или несчастные случаи. <br><br>

                10.	Не забывайте о правильной подготовке перед занятиями в бассейне. Разминка и растяжка помогут избежать мышечных травм и улучшить гибкость. <br><br>

                11.	Не забывайте о правильной технике плавания. Неправильное выполнение движений может привести к травмам или утомлению. Поэтому рекомендуется обратиться к инструктору или тренеру, чтобы получить индивидуальные рекомендации и коррекцию техники. <br><br>

                Соблюдение всех указанных правил и мер предосторожности поможет избежать возможных травм и обеспечит безопасную и эффективную тренировку для всех участников.
            </div>
        </div>

        <div v-if="currentMenuItem === 'types'" class="text-primary fw-bold">
            Классификация видов спортивного плавания
            <div class="text-secondary fw-normal p-0 mt-3 mb-4">
                В качестве спортивной дисциплины спортивное плавание представляет собой разнообразные виды соревнований, которые могут проводиться как в бассейне (25 м/50 м), так и на открытой воде. Главным условием является преодоление определенного расстояния (5/10/25 км на открытой воде и 50-1500 м в бассейне) обозначенным способом. Задача – приплыть к финишу первым за короткий промежуток времени. Основными установленными стилями спортивного плавания являются следующие: <br><br>
                    •	вольный стиль или кроль на груди; <br>
                    •	баттерфляй или дельфин; <br>
                    •	брасс; <br>
                    •	кроль на спине. <br><br>

                Как спортивный вид плавание первый раз было представлено в 1896 году на Олимпиаде в Афинах. <br><br>
                Рассмотрим подробнее, какие существующие виды спортивного плавания наиболее популярны, какой стиль плавания быстрее и другие особенности стилей спортивного плавания. <br><br>

                <span class="text-primary">Брасс</span> <br><br>

                    •	Техника: выполнение симметричных толчковых движений ногами и полукруговых руками в одной плоскости. Вдох осуществляется ртом путем подъема головы и плеч над водой. Выдох происходит в воде. <br>
                    •	Особенности: один из самых древних, медленных и энергоэффективных способов плавания.<br>
                    •	Плюсы: в данном процессе задействованы ок. 70 % мышц, меньше тратится энергии и сил, чем при плавании другими стилями.<br>
                    •	Минусы: невысокая скорость, тяжело освоить технику с первого раза.<br><br>

                    <span class="text-primary">Баттерфляй</span> <br><br>

                    •	Техника: одновременное выполнение симметричных волнообразных движений тела, при котором руки осуществляют сильный гребок с подъемом тела над водой, а ноги – удар (работают как «хвост»). Вдох происходит путем подъема плечевого пояса и головы над водой, выдох – в воду.<br>
                    •	Особенности: второй по скорости и сложнейший стиль плавания, требующий от пловца выносливости и хорошей физической подготовки.<br>
                    •	Плюсы: эффектный и красивый стиль, позволяет развивать хорошую скорость в процессе плавания.<br>
                    •	Минусы: большие затраты сил и энергии, сложная техника, не рекомендуется начинающим пловцам.<br><br>

                    <span class="text-primary">Кроль на груди</span> <br><br>

                    •	Техника: одновременное выполнение движений прямыми ногами (махи) и руками, поочередно делающими взмах и гребок под водой до бедра. Вдох осуществляется путем поворота в сторону головы, а выдох (носом и ртом) – в воду.<br>
                    •	Особенности: данный стиль самый быстрый и высококоординированный.<br>
                    •	Плюсы: возможность развить максимальную скорость плавания, легок для освоения. <br>
                    •	Минусы: требует точной координации движения рук и ног. <br><br>

                    <span class="text-primary">Кроль на спине</span> <br><br>
            
                    •	Техника: не имеет принципиального отличия от кроля на груди, помимо того, что выполняется на идеально выпрямленной спине. <br>
                    •	Особенности: данный способ предпочтителен для укрепления мышц спины. <br>
                    •	Плюсы: несложный в усвоении техники, помогает снять напряжение в мышцах. <br>
                    •	Минусы: перед освоением кроля на спине желательно изучить технику плавания кролем на груди. <br>
            </div>
        </div>

        <div v-if="currentMenuItem === 'other'" class="text-primary fw-bold">
            Другие виды плавания
            <div class="text-secondary fw-normal p-0 mt-3 mb-4">
                Плавание представляет собой процесс преодоления водного расстояния путем скоординированных специальным образом комплексов движения. Несмотря на популярность спортивных стилей плавания несомненным успехом пользуются и другие разновидности, которые больше выполняют роль упражнений, нежели полноценных стилей плавания: <br> <br>
                    •	по-собачьи;  <br>
                    •	на боку (овер-арм);  <br>
                    •	лягушка;  <br>
                    •	комбинированный способ и пр.  <br> <br>

                <span class="text-primary">Подводное плавание и его подвиды </span> <br> <br>
                Одним из разновидностей прикладного вида плавания является подводное. Данный термин включает в себя как отдельный вид подводного спорта, так и способы передвижения под водой: <br> <br>
                    •	с аквалангом;  <br>
                    •	фридайвинг (апноэ);  <br>
                    •	спортивный дайвинг; <br>
                    •	в ластах; <br>
                    •	ориентирование; <br>
                    •	спортивная стрельба; <br>
                    •	борьба (акватлон); <br>
                    •	игровые виды (хоккей, регби); <br>
                    •	охота, туризм и пр.
            </div>
        </div>

        <div v-if="currentMenuItem === 'unit'" class="text-primary fw-bold">
            Экипировка для плавания
            <div class="text-secondary fw-normal p-0 mt-3 mb-4">
                <span class="text-primary">Сланцы</span> <br><br>

                Обувь в бассейне не играет ключевую роль. Однако не следует недооценивать качество сланцев. <br>
                На сегодняшний день в магазинах можно найти любую модель - изящные модели для женщин, практичные для мужчин, удобные детские и даже модели, предназначенные для хождения по корралам, камням и песку, чего в бассейне Вы, конечно, не встретите, но купив такую обувь на летнем отдыхе, можно смело продолжать использовать ее и в городских условиях. <br>
                Главное при выборе обуви - это рельефная подошва для лучшего "сцепления" со скользкой поверхностью, особенно это касается детских моделей. Так же при выборе сланцев для ребенка желательно, чтобы они имели регулируемый ремешок для хорошей фиксации ноги. <br>
                Если Вы намерены посещать бассейн постоянно, то лучше выбирать сланцы проверенного качества, иначе, сланцы не предназначенные для постоянного контакта с водой прослужат недолго.<br><br>

                <span class="text-primary">Очки для плавания</span> <br><br>

                При выборе очков для плавания, главное, чтобы они плотно прилегали, но не сдавливали.
                Сегодня многие модели очков выпускаются с затылочной клипсой для упрощения регулировки. Существуют так же модели с саморегулирующейся переносицей.<br>
                Для любителей подводного плавания лучший вариант - приобрести универсальную модель, с широким обзором, благодаря чему отлично подходящую как для тренировок в бассейне, так и для отдыха на море, где она может быть использована как маска для плавания.<br>
                Для детей лучше выбирать очки с гипоаллергенной оправой из силикона. Желательно, чтобы линзы были из поликарбоната с защитой от разбития на мелкие осколки. <br><br>

                <span class="text-primary">Шапочки для плавания</span> <br><br>

                При выборе шапочки нужно выбирать ту, которая не будет морщинить и собираться на макушке. <br>
                Таковыми являются силиконовые шапочки сферической формы. <br>
                Для детей лучше выбирать модели из полиамида и эластомера. Этот материал идеален для детских волос и не давит на голову. Хорошо держит длинные волосы.<br><br>

                <span class="text-primary">Купальники</span> <br><br>

                Выбрать купальник для женщины не легче, чем любую другу вещь, будь то платье, юбка или плащ. Конечно, если поход в бассейн - это средство поддержании фигуры, здоровья или просто расслабление, то купальник Вы скорее всего купите в том случае, если он понравится внешне.<br>
                Но, возможно, Вам будет интересно узнать следующее: <br>
                Купальники бывают сплошными и раздельными, пляжными и спортивными. При чем спортивные купальники могут быть только сплошными. Если Ваш инструктор разрешит Вам попрыгать с вышки или Вам захочется исполнить танцевальное па под водой, лучше делайте это в сплошном купальнике. По крайней мере Вы будете уверены, что ни одна его часть не уплывет от вас при ударе об воду.<br>
                Так же спортивный купальник обеспечивает лучшее облегание и скольжение под водой. При выборе купальника обратите внимание на содержание лайкры, если оно более 25%, то материал будет великолепно «прилипать» к телу, не вызывая чувства стеснения.<br><br>

                <span class="text-primary">Аксессуары для плавания </span><br><br>

                Сегодня существует много различных приспособлений для плавания, которые помогут быстрее научиться плавать и свои посещения в бассейн делать более результативными.<br>
                Такая вещь, как калабашка обеспечивает необходимый уровень плавучести, что позволяет работать над гребком.<br>
                Доска для плавания предназначена для тренировки ног.<br>
                При выборе такого рода инвентаря, обратите внимание, чтобы края были закруглены. Это поможет предотвратить натирания кожи при частом использовании.<br>
                Чтобы препятствовать попаданию воды в ушной канал используйте беруши, а чтобы избежать попадания воды в нос - зажим.<br>
            </div>
        </div>

        <div v-if="currentMenuItem === 'test'" class="text-primary fw-bold">
            Тест
            <div class="text-secondary fw-normal p-0 mt-3 mb-4">
                
            </div>
        </div>

    </div>
</template>

