import { createRouter, createWebHistory  } from 'vue-router';
import home from "@/views/home.vue";
import book from "@/views/book.vue";
import activity from "@/views/activity.vue";
import stopwatch from "@/views/stopwatch.vue";
import activityDistance from "@/views/activity-distance.vue";
import ActivityAdd from "@/views/activity-add.vue";
import activityStyle from "@/views/activity-style.vue";
import activityRepetitions from "@/views/activity-repetitions.vue";
import ActivityTo from "@/views/activityTo.vue";
const routes = [
     {
        path: '/',
        name: 'home',
        component: home,
        meta: { title: 'Главная' },
        props: true
    },
    {
        path: '/book',
        name: 'book',
        component: book,
        meta: { title: 'Теория' },
        props: true
    },
    {
        path: '/activity',
        name: 'activity',
        component: activity,
        meta: { title: 'Активность' },
        props: true,
    },
    {
        path: '/activity_to',
        name: 'activityTo',
        component: ActivityTo,
        meta: { title: 'Активность' },
        props: true,
    },
    {
        path: '/add_activity',
        name: 'addActivity',
        component: ActivityAdd,
        meta: { title: 'Активность' },
        props: true,
        redirect: { name: 'activityDistance' },
        children: [
            {
                path: '',
                name: 'activityDistance',
                component: activityDistance,
                props: true
            },
            {
                path: '/style',
                name: 'activityStyle',
                component: activityStyle,
                props: true
            },
            {
                path: '/repetitions',
                name: 'activityRepetitions',
                component: activityRepetitions,
                props: true
            },
        ]
    },
    {
        path: '/stopwatch',
        name: 'stopwatch',
        component: stopwatch,
        meta: { title: 'Нормативы' },
        props: true
    }
];

const router = createRouter({
    history: createWebHistory(),
    linkActiveClass: 'is-subactive',
    linkExactActiveClass: 'is-active',
    routes
});
export default router;
