<template>
    <div class="w-100 flex-grow-1 justify-content-between py-5 d-flex flex-column align-items-center">
        <div class="d-flex text-primary flex-column align-items-center mb-3 activity__value fw-bold">
            {{ activity.repetition }}
            <span class="h6 fw-normal">Количество повторений</span>
        </div>
        <div class="d-flex flex-column w-100 align-items-center">
            <div class="row gap-2 mb-4 col-12 justify-content-center">
                <button class="activity__btn col-3" @click="addRepetition(1)">1</button>
                <button class="activity__btn col-3" @click="addRepetition(2)">2</button>
                <button class="activity__btn col-3" @click="addRepetition(3)">3</button>
                <button class="activity__btn col-3" @click="addRepetition(4)">4</button>
                <button class="activity__btn col-3" @click="addRepetition(5)">5</button>
                <button class="activity__btn col-3" @click="addRepetition(6)">6</button>
            </div>
            <div class="d-flex gap-2 align-items-center">
                <button class="activity__tools" @click="addRepetition(-1)">
                    <img src="@/assets/img/dash-lg.svg" alt="">
                </button>
                <div class="activity__tools activity__tools--more text-primary">
                    1
                </div>
                <button class="activity__tools" @click="addRepetition(1)">
                    <img src="@/assets/img/plus-lg.svg" alt="">
                </button>
            </div>
        </div>
        <div class="w-100 d-flex justify-content-between">
            <button class="activity__btn px-5" @click="next('activityStyle')">Назад</button>
            <button class="activity__btn  activity__btn--primary px-5" @click="showModal()">Создать</button>
        </div>
        <b-modal v-model="isShowModal" hideHeader="true" hideFooter="true" title="Создание тренировки"
                 class="text-primary">
            <div class="d-flex flex-column align-items-center">
                Придумайте название тренировки
                <div class="d-flex flex-column mt-3 gap-1 text-secondary mb-3 w-100">
                    <label class="text-secondary">Название тренировки</label>
                    <input class="form-control" style="font-size: 12px;" v-model="activity.name" type="text">
                </div>
                <div class="d-flex justify-content-end w-100">
                    <button class="btn p-0" @click="next('activity')">
                        <img src="@/assets/img/check-lg.svg" alt="">
                    </button>
                </div>
            </div>
        </b-modal>
    </div>
</template>

<script>
import {format} from 'fecha';

export default {
    name: "ActivityRepetitions",
    data() {
        return {
            activity: null,
            isShowModal: false,
        }
    },
    created() {
        this.activity = this.$store.state.activity;
    },
    methods: {
        addRepetition(value) {
            if (this.activity.repetition + value >= 0) {
                this.activity.repetition += value;
            } else {
                this.activity.repetition = 0;
            }
            this.$store.dispatch('changeActivity', this.activity);
        },
        showModal() {
            this.isShowModal = true;
        },
        next(name = 'activity') {
            if (name === 'activity') {
                this.activity = {...this.activity, id: Date.now(), date: format(new Date(), 'DD.MM.YYYY')};
                this.$store.dispatch('addActivityList', this.activity);
                this.$store.dispatch('addActivity');
            }
            this.$router.push({name: name});
        }
    }
}
</script>
