<template>
    <div class="wrapper min-vh-100 d-flex flex-column container">
        <div class="d-flex align-items-center mb-3">
            <div class="mx-auto ps-3">Тренировки</div>
            <button @click="next()" class="btn p-0">
                <img src="@/assets/img/plus-lg.svg" alt=""/>
            </button>
        </div>
        <div v-if="activityList.length===0"
             class="align-items-center justify-content-center flex-grow-1 d-flex flex-column">
            <div class="text-secondary">
                У вас ещё нет ни одной тренировки
            </div>
        </div>
        <div v-else v-for="activity in activityList" :key="activity.id"
             class="d-flex justify-content-between align-items-center mb-2" @click="next('activityTo',activity.id)">
            <div class="d-flex flex-column text-primary">
                {{ activity.name === '' ? 'Тренировка' : activity.name }}
                <span class="text-secondary">{{ activity.date }}</span>
            </div>
            <img src="@/assets/img/chevron-right.svg" alt=""/>
        </div>
    </div>
</template>

<script>
export default {
    name: "ActivityPage",
    data() {
        return {
            activityList: null
        }
    },
    created() {
        this.activityList = this.$store.state.activityList;
    },
    methods: {
        next(name = 'addActivity', data) {
            if (name === 'addActivity') {
                this.$store.dispatch('addActivity');
            }
            this.$router.push({name: name, query: {id: data}});
        }
    }
}
</script>