<template>
    <div class="container py-4">
        <div class="d-flex justify-content-between">
            <button class="btn p-0" @click="back">
                <img src="@/assets/img/arrow.svg" alt="">
            </button>
            <div>Тренировки</div>
            <button class="btn p-0" @click="removeActivity">
                <img src="@/assets/img/trash.svg" alt="">
            </button>
        </div>
        <div class="mt-4">
            <div class="text-primary border-bottom pb-2 mb-2">
                {{ activity.name === '' ? 'Тренировка' : activity.name }}
            </div>
            <div class="d-flex align-items-center gap-1 border-bottom pb-2 mb-2">
                <img src="@/assets/img/calendar-date.svg" alt=""/>
                <span>
                    {{ activity.date }}
                </span>
            </div>
            <div v-if="activity.style.brass!==''" class="mb-1 d-flex gap-1">
                <div class="text-primary">
                    {{activity.repetition}}<span>x</span>
                </div>
                <div>
                    {{activity.distance}} Брасс {{activity.style.brass}}
                </div>
            </div>
            <div v-if="activity.style.delfin!==''" class="mb-1 d-flex gap-1">
                <div class="text-primary">
                    {{activity.repetition}}<span>x</span>
                </div>
                <div>
                    {{activity.distance}} Дельфин {{activity.style.brass}}
                </div>
            </div>
            <div v-if="activity.style.krol!==''" class="mb-1 d-flex gap-1">
                <div class="text-primary">
                    {{activity.repetition}}<span>x</span>
                </div>
                <div>
                    {{activity.distance}} Кроль {{activity.style.brass}}
                </div>
            </div>
            <div v-if="activity.style.spina!==''" class="mb-1 d-flex gap-1">
                <div class="text-primary">
                    {{activity.repetition}}<span>x</span>
                </div>
                <div>
                    {{activity.distance}} Спина {{activity.style.brass}}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "ActivityTo",
    data() {
        return {
            activity: null
        }
    },
    created() {
        this.activity = this.$store.state.activityList.filter(item => item.id == this.$route.query.id)[0];
    },
    methods: {
        back() {
            this.$router.go(-1);
        },
        removeActivity(){
            this.$store.dispatch('removeActivity',this.$route.query.id);
            this.back();
        }
    }
}
</script>
