<template>
    <div class="w-100 style flex-grow-1 justify-content-between py-5 d-flex flex-column align-items-center">
        <div class="text-primary fw-bold fs-6">Стиль</div>
        <div class="container w-100 d-flex flex-column gap-2">
            <div class="d-flex justify-content-between">
                <div>Брасс</div>
                <div class="d-flex gap-2">
                    <button @click="changeSelector('brass','П')"
                            :class="`style__btn ${activity.style.brass==='П'?'style__btn--active':''}`">П
                    </button>
                    <button @click="changeSelector('brass','Р')"
                            :class="`style__btn ${activity.style.brass==='Р'?'style__btn--active':''}`">Р
                    </button>
                    <button @click="changeSelector('brass','Н')"
                            :class="`style__btn ${activity.style.brass==='Н'?'style__btn--active':''}`">Н
                    </button>
                    <button @click="changeSelector('brass','У')"
                            :class="`style__btn ${activity.style.brass==='У'?'style__btn--active':''}`">У
                    </button>
                </div>
            </div>
            <div class="d-flex justify-content-between">
                <div>Дельфин</div>
                <div class="d-flex gap-2">
                    <button @click="changeSelector('delfin','П')"
                            :class="`style__btn ${activity.style.delfin==='П'?'style__btn--active':''}`">П
                    </button>
                    <button @click="changeSelector('delfin','Р')"
                            :class="`style__btn ${activity.style.delfin==='Р'?'style__btn--active':''}`">Р
                    </button>
                    <button @click="changeSelector('delfin','Н')"
                            :class="`style__btn ${activity.style.delfin==='Н'?'style__btn--active':''}`">Н
                    </button>
                    <button @click="changeSelector('delfin','У')"
                            :class="`style__btn ${activity.style.delfin==='У'?'style__btn--active':''}`">У
                    </button>
                </div>
            </div>
            <div class="d-flex justify-content-between">
                <div>Кроль</div>
                <div class="d-flex gap-2">
                    <button @click="changeSelector('krol','П')"
                            :class="`style__btn ${activity.style.krol==='П'?'style__btn--active':''}`">П
                    </button>
                    <button @click="changeSelector('krol','Р')"
                            :class="`style__btn ${activity.style.krol==='Р'?'style__btn--active':''}`">Р
                    </button>
                    <button @click="changeSelector('krol','Н')"
                            :class="`style__btn ${activity.style.krol==='Н'?'style__btn--active':''}`">Н
                    </button>
                    <button @click="changeSelector('krol','У')"
                            :class="`style__btn ${activity.style.krol==='У'?'style__btn--active':''}`">У
                    </button>
                </div>
            </div>
            <div class="d-flex justify-content-between">
                <div>На спине</div>
                <div class="d-flex gap-2">
                    <button @click="changeSelector('spina','П')"
                            :class="`style__btn ${activity.style.spina==='П'?'style__btn--active':''}`">П
                    </button>
                    <button @click="changeSelector('spina','Р')"
                            :class="`style__btn ${activity.style.spina==='Р'?'style__btn--active':''}`">Р
                    </button>
                    <button @click="changeSelector('spina','Н')"
                            :class="`style__btn ${activity.style.spina==='Н'?'style__btn--active':''}`">Н
                    </button>
                    <button @click="changeSelector('spina','У')"
                            :class="`style__btn ${activity.style.spina==='У'?'style__btn--active':''}`">У
                    </button>
                </div>
            </div>
        </div>
        <div class="w-100 d-flex flex-column gap-2 px-2">
            <div class="d-flex align-items-center gap-2">
                <button class="style__btn">П</button>
                Полная координация
            </div>
            <div class="d-flex align-items-center gap-2">
                <button class="style__btn">Р</button>
                Руки
            </div>
            <div class="d-flex align-items-center gap-2">
                <button class="style__btn">Н</button>
                Ноги
            </div>
            <div class="d-flex align-items-center gap-2">
                <button class="style__btn">У</button>
                Упражнения
            </div>

        </div>
        <div class="w-100 d-flex justify-content-between">
            <button class="activity__btn px-5 " @click="next('activityDistance')">Назад</button>
            <button class="activity__btn  activity__btn--primary px-5" @click="next()">Далее</button>
        </div>
    </div>
</template>

<script>
export default {
    name: "ActivityStyle",
    data() {
        return {
            activity: null
        }
    },
    created() {
        this.activity = this.$store.state.activity;
    },
    methods: {
        changeSelector(list, value) {
            if (this.activity.style[list] === value) {
                this.activity.style[list] = '';
            } else {
                this.activity.style[list] = value;
            }
            this.$store.dispatch('changeActivity', this.activity);
        }, next(name = 'activityRepetitions') {
            this.$router.push({name: name});
        }
    }
}
</script>
