<template>
    <div class="w-100">
        <video ref="videoPlayer" class="video-js">
            <slot></slot>
        </video>
        <div class="d-flex flex-column fw-bold text-black mt-1 ms-2">
            {{options.title}}
            <span class="fw-normal text-secondary">{{options.time}}</span>
        </div>
    </div>
</template>

<script>
import 'video.js/dist/video-js.min.css'
import videojs from 'video.js';

export default {
    name:"VideoPlayer",
    data() {
        return {
            player: null
        }
    },
    props: {
        options: {
            type: Object,
            default() {
                return {};
            }
        },
    },
    mounted() {
        this.player = videojs(this.$refs.videoPlayer, this.options, () => {});
    },
}
</script>