<template>
    <div class="w-100 activity flex-grow-1 justify-content-between py-5 d-flex flex-column align-items-center">
        <b-toast class="position-absolute bg-white w-75" v-model="showToast">
            <div class="mb-3 text-secondary" style="font-size: 12px;">
                Вы действительно хотите отменить действие?
            </div>
            <div class="d-flex justify-content-end d-grid gap-2">
                <button class="btn p-0" @click="()=>{showToast = false}">
                    <img src="@/assets/img/x-lg.svg" alt="">
                </button>
                <button class="btn p-0" @click="this.$router.push({name:'activity'})">
                    <img src="@/assets/img/check-lg.svg" alt="">
                </button>
            </div>
        </b-toast>
        <div class="d-flex text-primary flex-column align-items-center mb-3 activity__value fw-bold">
            {{ activity.distance }}
            <span class="h6 fw-normal">Дистанция, м</span>
        </div>
        <div class="d-flex flex-column w-100 align-items-center">
            <div class="row gap-2 mb-4 col-12 justify-content-center">
                <button class="activity__btn col-3" @click="addDistance(12.5)">12,5</button>
                <button class="activity__btn col-3" @click="addDistance(25)">25</button>
                <button class="activity__btn col-3" @click="addDistance(50)">50</button>
                <button class="activity__btn col-3" @click="addDistance(100)">100</button>
                <button class="activity__btn col-3" @click="addDistance(200)">200</button>
                <button class="activity__btn col-3" @click="addDistance(400)">400</button>
            </div>
            <div class="d-flex gap-2 align-items-center">
                <button class="activity__tools" @click="addDistance(-25)">
                    <img src="@/assets/img/dash-lg.svg" alt="">
                </button>
                <div class="activity__tools activity__tools--more text-primary">
                    25
                </div>
                <button class="activity__tools" @click="addDistance(25)">
                    <img src="@/assets/img/plus-lg.svg" alt="">
                </button>
            </div>
        </div>
        <div class="w-100 d-flex justify-content-between">
            <button class="activity__btn px-5 " @click="next('activity')">Назад</button>
            <button class="activity__btn  activity__btn--primary px-5" @click="next()">Далее</button>
        </div>
    </div>
</template>

<script>
export default {
    name: "activityDistance",
    data() {
        return {
            activity: null,
            showToast: false
        }
    },
    created() {
        this.activity = this.$store.state.activity;
    },
    methods: {
        addDistance(value) {
            if (this.activity.distance + value >= 0) {
                this.activity.distance += value;
            } else {
                this.activity.distance = 0;
            }
            this.$store.dispatch('changeActivity', this.activity);
        },
        next(name = 'activityStyle') {
            if (name === 'activity' && this.activity.distance !== 0) {
                this.showMessage();
            } else if (!this.showToast && this.activity.distance !== 0) {
                this.$router.push({name: name});
            }
        },
        showMessage() {
            this.showToast = true;
        }
    }
}
</script>