import {createStore} from 'vuex';
import VuexPersist from 'vuex-persist'

const vuexPersist = new VuexPersist({
    key: 'swim',
    storage: window.localStorage
});

const activityTemplate = () => {
    return {
        distance: 0,
        style: {
            brass: '',
            delfin: '',
            krol: '',
            spina: ''
        },
        repetition: 1,
        id: '',
        date: '',
        name: ''
    }
};

const store = createStore({
    state: {
        activityList: [],
        user: {
            name: '',
            age: '',
            weight: '',
            height: ''
        }
    },
    plugins: [vuexPersist.plugin],
    mutations: {
        ADD_ACTIVITY(state) {
            state.activity = activityTemplate();
        },
        CHANGE_ACTIVITY(state, params) {
            state.activity = params;
        },
        CHANGE_USER(state, params) {
            state.user = params;
        },
        ADD_ACTIVITY_LIST(state, params) {
            state.activityList.push(params);
        },
        REMOVE_ACTIVITY_LIST(state, params) {
            state.activityList = state.activityList.filter(item => item.id != params);
        }
    },
    actions: {
        addActivity(context) {
            context.commit('ADD_ACTIVITY');
        },
        changeActivity(context, params) {
            context.commit('CHANGE_ACTIVITY', params);
        },
        addActivityList(context, params) {
            context.commit('ADD_ACTIVITY_LIST', params);
        },
        removeActivity(context, params) {
            context.commit('REMOVE_ACTIVITY_LIST', params);
        },
        changeUser(context, params) {
            context.commit('CHANGE_USER', params);
        }
    }
});

export default store;


