<template>
    <div class="timer">
        <div class="timer__cover mb-4">
            <img class="w-100" src="@/assets/img/timerGroup.png" alt=""/>
            <div class="timer__content text-primary">
                <span>{{ timerHours }}</span>
                <span>{{ timerMinutes }}</span>
                <span>{{ timerSecond }}</span>
            </div>
<!--            <p v-for="item in 12" :class="'time time_'+ item" :key="item+Date.now()"></p>-->
        </div>
        <div class="timer__tools">
            <b-button v-if="isStart" variant="primary"
                      @click="pauseTimer()">
                <img src="@/assets/img/pause-fill.svg" alt=""/>
            </b-button>
            <b-button v-else variant="primary"
                      @click="startTimer()">
                <img src="@/assets/img/play-timer.svg" alt=""/>
            </b-button>
            <b-button variant="primary" @click="restartTimer()">
                <img src="@/assets/img/arrow-clockwise.svg" alt=""/>
            </b-button>
        </div>
    </div>
</template>

<script>
import {useStopwatch} from 'vue-timer-hook';

export default {
    name: "TimerComponent",
    data() {
        return {
            timer: null,
            isStart: null
        }
    },
    computed: {
        timerHours() {
            if (!this.timer) {
                return '00'
            }
            return this.timer?.hours < 10 ? '0' + this.timer?.hours : this.timer?.hours;
        },
        timerMinutes() {
            if (!this.timer) {
                return '00'
            }
            const minutes = this.timer?.minutes - 1;
            return minutes < 10 ? '0' + minutes : minutes;
        },
        timerSecond() {
            if (!this.timer) {
                return '00'
            }
            return this.timer?.seconds < 10 ? '0' + this.timer?.seconds : this.timer?.seconds;
        }
    },
    mounted() {
    },
    methods: {
        startTimer() {
            if (this.isStart) {
                this.isStart = true;
                this.timer.start();
            } else {
                this.restartTimer();
                this.isStart = true;
                this.timer.start();
            }

        },
        pauseTimer() {
            this.isStart = false;
            this.timer.pause();
        },
        restartTimer() {
            this.isStart = false;
            this.timer = useStopwatch();
            this.pauseTimer();
        }
    }
}
</script>
