<template>
    <div v-if="isShowModal" class="position-absolute d-block h-100 w-100"
         style="z-index: 1000; background-color: #212529d9">
        <div class="modal fade show text-primary d-block">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-title text-dark d-flex mx-auto mt-2" style="font-size: 14px;">Аккаунт</div>
                    <div class="d-flex flex-column align-items-center w-100 py-3 px-4">
                        <div
                            class="d-flex d-grid gap-3 input-group-sm justify-content-between w-100 align-items-center mb-2">
                            <label for="name" class="form-label text-secondary">Имя</label>
                            <input @change="changeUser()" id="name" class="form-control" style="font-size: 12px;"
                                   v-model="user.name" type="text">
                        </div>
                        <div
                            class="d-flex d-grid gap-3 input-group-sm justify-content-between w-100 align-items-center mb-2">
                            <label for="age" class="form-label text-secondary">Возраст</label>
                            <input @change="changeUser()" id="age" class="form-control" style="font-size: 12px;"
                                   v-model="user.age" type="number">
                        </div>
                        <div
                            class="d-flex d-grid gap-3 input-group-sm justify-content-between w-100 align-items-center mb-2">
                            <label for="weight" class="form-label text-secondary">Вес</label>
                            <input @change="changeUser()" id="weight" class="form-control" style="font-size: 12px;"
                                   v-model="user.weight" type="number">
                        </div>
                        <div
                            class="d-flex d-grid gap-3 input-group-sm justify-content-between w-100 align-items-center mb-2">
                            <label for="height" class="form-label text-secondary">Рост</label>
                            <input @change="changeUser()" id="height" class="form-control" style="font-size: 12px;"
                                   v-model="user.height" type="number">
                        </div>
                        <b-button class="ms-auto mt-2" style="font-size: 13px;" variant="primary"
                                  @click="handlerModal()">Начать тренировки
                        </b-button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div class="home">
        <div class="pt-5 home__top d-flex flex-column align-items-center">
            <div class="container home__greetings mb-4">
                <div class="home__greetings-title text-white mb-2">Здравствуйте, {{ user.name }}</div>
                <div class="home__greetings-date text-white">{{
                        (new Date()).toLocaleDateString('ru-RU', {
                            weekday: "long",
                            month: "long",
                            day: "numeric"
                        }).replace(/^\p{L}/u, (c) => c.toUpperCase())
                    }}
                </div>
            </div>
            <div class="bg-white home__quote mt-2 p-3">
                « Многие люди терпят неудачу только потому, что сдаются в двух шагах от успеха » - Саймон Хартли
            </div>
        </div>
        <div class="home__body ms-2 me-2 mt-2">
            <div class="home__body-title mb-2">Тренировка дня</div>
            <div class="training d-flex" @click="showVideo()">
                <div>
                    <div class="training__title">Розалия Насретдинова. Тренировка по плаванию от чемпионки мира</div>
                    <div class="training__desc"><span>5:33</span></div>
                </div>
                <div class="my-auto w-auto">
                    <img class="w-100" src="@/assets/img/play.svg">
                </div>
                <div :class="!showActive?'d-none':'d-block'">
                    <video ref="videoPlayer" class="video-js">
                        <source src="@/assets/videos/3.mp4" type="video/mp4">
                    </video>
                </div>
            </div>
        </div>
        <div class="home__bottom ms-2">
            <div class="home__bottom-title mt-3 mb-2">Популярные программы</div>
        </div>
        <div class="d-flex overflow-scroll px-2 gap-2 pb-2">
            <div>
                <VideoPlayer :options="{
                    title:'Как научиться плавать С НУЛЯ?',
                    autoplay: false,
                    controls: true,
                    time:'5:13'
                }">
                    <source src="@/assets/videos/1.mp4" type="video/mp4">
                </VideoPlayer>
            </div>
            <div>
                <VideoPlayer :options="{
                    title:'10 приёмов в плавании на спине',
                    autoplay: false,
                    controls: true,
                    time:'5:46'
                }">
                    <source src="@/assets/videos/2.mp4" type="video/mp4">
                </VideoPlayer>
            </div>
            <div>
                <VideoPlayer :options="{
                    title:'Как ПРАВИЛЬНО плавать, чтобы не вредить здоровью спины и шеи',
                    autoplay: false,
                    controls: true,
                    time:'6:35'
                }">
                    <source src="@/assets/videos/4.mp4" type="video/mp4">
                </VideoPlayer>
            </div>
            <div>
                <VideoPlayer :options="{
                    title:'Как плавание влияет на наш организм',
                    autoplay: false,
                    controls: true,
                    time:'3:48'
                }">
                    <source src="@/assets/videos/5.mp4" type="video/mp4">
                </VideoPlayer>
            </div>
        </div>
    </div>
</template>

<script>

import VideoPlayer from "@/components/videoPlayer.vue";
import videojs from "video.js";

export default {
    name: "HomePage",
    components: {VideoPlayer},
    data() {
        return {
            user: null,
            isShowModal: true,
            player: null,
            showActive: false
        }
    },
    created() {
        this.user = this.$store.state.user;
        this.isShowModal = this.user.name.trim() === '';
    },
    mounted() {
        this.player = videojs(this.$refs.videoPlayer, {
            autoplay: false,
            controls: true,
            disablePictureInPicture: true,
        }, () => {
            this.player.on('fullscreenchange', this.hiddenVideo);
        });

    },
    methods: {
        handlerModal() {
            if (this.user.name.trim() !== '') {
                this.isShowModal = false;
            }
        },
        changeUser() {
            this.$store.dispatch('changeUser', this.user);
        },
        showVideo() {
            this.showActive = true;
            this.player.play();
            this.player.requestFullscreen();
        },
        hiddenVideo() {
            if (!this.player.isFullscreen()) {
                this.player.pause();
                this.showActive = false;
            }
        }
    }

}
</script>